  :root {
    --color1: rgb(26, 2, 50);
    --color2: rgb(40, 4, 75);
    --color3: rgb(65, 0, 130);
    --color4: rgb(166, 80, 252);
    --color5: rgb(204, 153, 255);
    --color6: rgb(240, 224, 255);
    --color7: rgb(237, 219, 255);

    --negative: red;
    --active-dark: var(--color3);
    --active-light: var(--color7);
  }

  *,
  :after,
  :before {
    box-sizing: border-box;
  }

  body {
    margin: 0;
    font-size: 20px;
  }

  body,
  button,
  input,
  select {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 20px;
  }

  .ex-button {
    position: absolute;
    right: 0;
  }

  .almost-half {
    height: 43vh;
