<template>
  <section class="chess-end__body">
    <div>
      <h1>Game over?</h1>

      <template v-if="notation.length">
        <div v-html="notation" class="chess-end__pgn monospace" />
        <button @click="handleCopy">Copy Game Notes {{ copySuccessMessage }}</button>
        <a href="chess.com" hidden>Save game in Chess.com</a>
      </template>
    </div>

      <div class="chess-end__actions">
        <button @click="$emit('reset')">↺ Reset game</button>
        <button @click="$emit('resume')">Keep playing</button>
      </div>

  </section>
</template>

<script>
  export default {
    name: 'ChessEnd',
    props: {
      notation: {
        type: String,
        default: undefined
      }
    },
    data() {
      return {
        copySuccess: null 
      }
    },
    methods: {
      handleCopy() {
        navigator.clipboard.writeText(this.notation).then(
          () => {
            this.copySuccess = true;
          },
          () => {
            this.copySuccess = false;
          }
        );
      }
    },
    computed: {
      copySuccessMessage() {
        if (this.copySuccess === true)
          return '✔';
        else if (this.copySuccess === false)
          return 'X';
        else
          return '';
      }
    }
  }
</script>

<style>
.chess-end__body {
  display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
}

.chess-end__actions {
  display: flex;
  flex-direction: column;
}

.chess-end__actions button {
  padding: 20px;
}

.chess-end__pgn {
  background: #eee;
  padding: 10px;
  border-radius: 3px;
  font-size: 14px;
}
</style>