<template>
  <section class="chess-controls">
    <button 
      v-if="isPaused"
      title="Information"
      @click="$emit('info')"
    >
    ❓
    </button>
    <button v-if="gameStarted" @click="$emit('togglePause')">
      <template v-if="isPaused">
        <span class="visually-hidden">play</span>
        ▶️
      </template>
      <template v-else>
        <span class="visually-hidden">pause</span>
        ⏸
      </template>
    </button>
    <template v-if="isPaused">
      <button v-if="gameStarted" @click="$emit('stop')">
        ⏹
      </button>
      <button 
        v-else 
        title="New Clock Settings"
        @click="$emit('settings')"
      >
      ⚙️
      </button>
    
    </template>
  </section>
</template>

<script>
  export default {
    name: 'ChessControls',
    props: {
      isPaused: {
        type: Boolean,
        default: false
      },
      gameStarted: {
        type: Boolean,
        default: false
      }
    }
  }
</script>

<style>
.chess-controls {
  display: flex;
}

.chess-controls button {
  border: 0;
  background: #aaa;
  width: 100%;
  font-size: 30px;
}
</style>