<template>
  <div class="chess-notation">
    <div class="chess-notation__results">
      <!-- {{ notation.join('') }} -->
      <template v-for="note, i in notation" :key="i">
        <button v-if="note.length" @click="goTo(i)">
          {{ note
            .replace('P', color === 'white' ? '♙' : '♟︎')
            .replace('R', color === 'white' ? '♖' : '♜')
            .replace('N', color === 'white' ? '♘' : '♞')
            .replace('B', color === 'white' ? '♗' : '♝')
            .replace('K', color === 'white' ? '♔' : '♚')
            .replace('Q', color === 'white' ? '♕' : '♛')
          }}
        </button>
      </template>
      <span v-if="notation[4].length === 0" class="chess-notation__button-placeholder">&nbsp;</span>
    </div>
    <div v-if="step === 0" class="chess-notation__piece">
      <div class="chess-notation__piece-proper">
        <button @click="notate('P', 3)" :class="{'is-active': notation[step] === 'P'}">&nbsp;</button>
        <button @click="notate('R', 3)" :class="{'is-active': notation[step] === 'R'}">R</button>
        <button @click="notate('N', 3)" :class="{'is-active': notation[step] === 'N'}">N</button>
        <button @click="notate('B', 3)" :class="{'is-active': notation[step] === 'B'}">B</button>
        <button @click="notate('K', 3)" :class="{'is-active': notation[step] === 'K'}">K</button>
        <button @click="notate('Q', 3)" :class="{'is-active': notation[step] === 'Q'}">Q</button>
      </div>
      <div v-if="canCastle" class="chess-notation__castling">
        <button @click="notate('0-0', 6)" :class="{'is-active': notation[step] === '0-0'}">King side castle</button>
        <button @click="notate('0-0-0', 6)" :class="{'is-active': notation[step] === '0-0-0'}">Queen side castle</button>
      </div>
    </div>
    <div v-if="step === 1 || step === 3"  class="chess-notation__file">
      <div class="chess-notation__file-proper">
        <button @click="notate('a', isSpecifyingOrigin ? 3 : 4)" :class="{'is-active': notation[step] === 'a'}">a</button>
        <button @click="notate('b', isSpecifyingOrigin ? 3 : 4)" :class="{'is-active': notation[step] === 'b'}">b</button>
        <button @click="notate('c', isSpecifyingOrigin ? 3 : 4)" :class="{'is-active': notation[step] === 'c'}">c</button>
        <button @click="notate('d', isSpecifyingOrigin ? 3 : 4)" :class="{'is-active': notation[step] === 'd'}">d</button>
        <button @click="notate('e', isSpecifyingOrigin ? 3 : 4)" :class="{'is-active': notation[step] === 'e'}">e</button>
        <button @click="notate('f', isSpecifyingOrigin ? 3 : 4)" :class="{'is-active': notation[step] === 'f'}">f</button>
        <button @click="notate('g', isSpecifyingOrigin ? 3 : 4)" :class="{'is-active': notation[step] === 'g'}">g</button>
        <button @click="notate('h', isSpecifyingOrigin ? 3 : 4)" :class="{'is-active': notation[step] === 'h'}">h</button>
      </div>
      <div class="chess-notation__file-controls">
        <button 
          @click="handleOriginClick" 
          :disabled="!['P', 'R', 'N', 'Q'].includes(notation[0])"
        >
          <input type="checkbox" :checked="isSpecifyingOrigin" disabled />
          Specify Origin
        </button>
      </div>
    </div>
    <div v-if="step === 2 || step === 4"  class="chess-notation__rank">
      <div class="chess-notation__rank-proper">
        <button @click="notate('1', isSpecifyingOrigin ? 2 : isPromoting('black') ? 5 : 6)" :class="{'is-active': notation[step] === '1'}">1</button>
        <button @click="notate('2', isSpecifyingOrigin ? 2 : 6)" :class="{'is-active': notation[step] === '2'}">2</button>
        <button @click="notate('3', isSpecifyingOrigin ? 2 : 6)" :class="{'is-active': notation[step] === '3'}">3</button>
        <button @click="notate('4', isSpecifyingOrigin ? 2 : 6)" :class="{'is-active': notation[step] === '4'}">4</button>
        <button @click="notate('5', isSpecifyingOrigin ? 2 : 6)" :class="{'is-active': notation[step] === '5'}">5</button>
        <button @click="notate('6', isSpecifyingOrigin ? 2 : 6)" :class="{'is-active': notation[step] === '6'}">6</button>
        <button @click="notate('7', isSpecifyingOrigin ? 2 : 6)" :class="{'is-active': notation[step] === '7'}">7</button>
        <button @click="notate('8', isSpecifyingOrigin ? 2 : isPromoting('white') ? 5 : 6)" :class="{'is-active': notation[step] === '8'}">8</button>
      </div>
      <div class="chess-notation__rank-controls">
        <button 
          v-if="step === 2"
          @click="handleOriginClick" 
          :disabled="!['P', 'R', 'N', 'Q'].includes(notation[0])"
        >
          <input type="checkbox" :checked="isSpecifyingOrigin" />
          Specify Origin
        </button>
      </div>
    </div>
    <div v-if="step === 5" class="chess-notation__promotion">
      <div class="chess-notation__promotion-label">Promote pawn to:</div>
      <div class="chess-notation__promotion-proper">
        <button @click="notate('=Q', 6)" :class="{'is-active': notation[step] === '=Q'}">=Q</button>
        <button @click="notate('=N', 6)" :class="{'is-active': notation[step] === '=N'}">=N</button>
        <button @click="notate('=R', 6)" :class="{'is-active': notation[step] === '=R'}">=R</button>
        <button @click="notate('=B', 6)" :class="{'is-active': notation[step] === '=B'}">=B</button>
      </div>
    </div>
    <div v-if="step === 6" class="chess-notation__final-results">
      <span>{{  formatNotation }}</span>
      <button @click="finish" class="chess-notation__confirm-button">✔</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ChessNotation',
    props: {
      color: {
        type: String,
        default: undefined
      }
    },
    data() {
      return {
        notation: ['','','','','',''],
        step: 0,
        isSpecifyingOrigin: false,
        canCastle: true
      }
    },
    methods: {
      notate(notation, step) {
        this.notation[this.step] = notation;
        this.step = step;
        this.isSpecifyingOrigin = false;
      },
      reset() {
        this.step = 0;
        this.notation = ['','','','',''];
      },
      finish() {
        if (this.notation[0].includes("0-0") || this.notation.includes('K'))
          this.canCastle = false;
        this.$emit('notate', this.formatNotation);
        this.reset();
      },
      handleOriginClick() {
        this.isSpecifyingOrigin = !this.isSpecifyingOrigin;

        // If origin file has not been specified go to it, otherwise go to origin rank
        if (this.notation[1].length === 0)
          this.step = 1;
        else
          this.step = 2;
      },
      isPromoting(color) {
        return color === this.color && this.notation[0] === 'P'; 
      },
      goTo(step) {
        this.step = step;
        if (step === 1 || step === 2)
          this.isSpecifyingOrigin = true;
        else
          this.isSpecifyingOrigin = false;
      }
    },
    computed: {
      formatNotation() {
        return this.notation.join('').replace('P', '');
      }
    }
  }
</script>

<style>

  .chess-notation button {
    border: 0;
    background: transparent;
  }

  .chess-notation__results {
    height: 5vh;
    background: #ddd;
  }

  .chess-notation__confirm-button {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    display: flex;
    align-items: end;
    justify-content: end;
  }

  .chess-notation__results button,
  .chess-notation__button-placeholder {
    font-size: 3vh;
    min-width: 10vw;
    border-radius: 3px;
    margin-left: 2px;
  }
  
  .chess-notation__results button {
    background: #bbb;
  }

  .chess-notation__button-placeholder {
    border: 2px dotted #aaa;
    display: inline-block;
    background: rgba(255,255,255,.2);
    position: relative;
  }

  @keyframes blink {
    0% { opacity: 1; }
    80% { opacity: 1; }
    100% { opacity: 0; }
  }

  .chess-notation__button-placeholder:before {
    content: '';
    width: 70%;
    height: 1px;
    border-bottom: 1px solid black;
    position: absolute;
    bottom: 5px;
    left: 15%;
    /* animation: blink 1s infinite; */
  }

  .chess-notation__piece,
  .chess-notation__rank,
  .chess-notation__file,
  .chess-notation__final-results,
  .chess-notation__promotion {
    height: calc(100% - 5vh);
  }

  .chess-notation__piece,
  .chess-notation__castling,
  .chess-notation__rank,
  .chess-notation__rank-controls,
  .chess-notation__file-controls,
  .chess-notation__file,
  .chess-notation__promotion {
    display: flex;
    flex-wrap: wrap;
  }

  .chess-notation__piece-proper,
  .chess-notation__file-proper,
  .chess-notation__rank-proper {
    width: 100%;
    height: 80%;
  }

  .chess-notation__promotion-label {
    height: 5vh;
  }

  .chess-notation__promotion-proper {
    width: 100%;
    height: calc(100% - 5vh);
  }

  .chess-notation__castling,
  .chess-notation__file-controls,
  .chess-notation__rank-controls {
    height: 20%;
    width: 100%;
  }

   .chess-notation__castling button,
   .chess-notation__promotion-proper button {
      width: 50%;
   }

   .chess-notation__file-controls button,
   .chess-notation__rank-controls button {
    width: 100%;
  }

  .chess-notation__piece-proper button,
  .chess-notation__file-proper button,
  .chess-notation__rank-proper button {
    width: 33.33%;
    height: 50%;
    outline: 1px solid black;
  }

  .chess-notation__promotion-proper button {
    height: 50%;
    outline: 1px solid black;
  }

  .chess-notation__file-proper button,
  .chess-notation__rank-proper button {
    width: 25%;
  }

  .chess-notation__final-results {
    font-size: 20vw;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .chess-notation__final-results span {
    display: flex;
  }

  .chess-notation button.is-active {
    background: var(--active-light);
  }
</style>