<template>
  <main>
    <h1>Chess Notepad</h1>

    <section>
      <h2>How Chess Notepad Works</h2>
      <p>Summary paragraph</p>

      <button>Full instructions</button>
      <section class="chess-notepad__full-instructions">
        <h3>How the Clock Works</h3>
        <p>Timer instructions will go here</p>

        <h3>How to Notate Your Moves</h3>
        <p>Instructions will go here</p>

        <h4>Step 1: Note the piece you moved</h4>
        <p>Instructions will go here</p>

        <h4>Steps 2 & 3: Note the square you moved to</h4>
        <p>Instructions will go here</p>

        <h4>Special Circumstances</h4>
        <p>Some circumstances require more information.</p>

        <h5>Disambiguating Pieces</h5>
        <p>Instructions will go here</p>

        <h5>Pawn Promotion</h5>
        <p>Instructions will go here</p>
      </section>
    </section>

    <section>
      <h2>Clocks</h2>
      <h3>Presets</h3>
      <ul id="clock-presets">
        <li>
          <a href="/game/1800">30</a>
        </li>
        <li>
          <a href="/game/900-10">15 | 10</a>
        </li>
        <li>
          <a href="/game/600">10</a>
        </li>
        <li>
          <a href="/game/300-5">5 | 5</a>
        </li>
        <li>
          <a href="/game/300">5</a>
        </li>
        <li>
          <a href="/game/180">3</a>
        </li>
      </ul>
      <button hidden>+ Custom Game Settings</button>
    </section>

    <section hidden>
      AD
    </section>
  </main>
</template>

<script>
  export default {
    name: 'ChessInfo',
    props: {
      gameStarted: {
        type: Boolean,
        default: false
      }
    },
    mounted() {
      document.querySelectorAll('#clock-presets a').forEach(a => {
        a.addEventListener('click', this.handleClick);
      });
    },
    methods: {
      handleClick(e) {
        // TODO: Make this not reload SPA
        e.preventDefault();
        const time = e.target.getAttribute('href').replace('/game/', '');
        if (this.gameStarted) {
          if (window.confirm("Do you want to reset your current game?"))
            window.location.pathname = time;
        } else
          window.location.pathname = time;
      }
    }
  }
</script>

